.container{
  display: flex;
  align-items: flex-start;
  gap: 6.25rem;
/*  padding: 7.5rem 5.875rem;*/
  height: 100%;
  justify-content: center;
  align-items: center;
  background: #F4F4F4;

/*  padding: 2% 18%;
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1.5px solid #000000;
  background-color: #F6F3F5;
  color: #000000;*/
}

#myName{
  display: flex;
  height: 36.9375rem;
  width: 17.5rem;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

#header{
  font-family: 'Xanh Mono', monospace;
  font-style: italic;
  font-weight: 400;
  color: #000;
  width: auto;
  font-size: 3.5rem;
  line-height: 88%; /* 3.08rem */
}

#pics{
  width: 17.5rem;
  height: 17.5rem;
  flex-shrink: 0;

  background: lightgray -35.401px -125.76px / 120% 160% no-repeat;
  mix-blend-mode: luminosity;
}

#pics:hover{
  mix-blend-mode: normal;
}

#tt1{
  display: flex;
  width: auto;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.125rem;
}

#t{
  color: #000;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
}

#b1{
  color: #000;
  font-family: Xanh Mono;
  font-size: 1.5rem;
  font-style: italic;
  font-weight: 400;
  line-height: normal;
}

#contents{
  display: flex;
  width: 44.5rem;
  flex-direction: column;
  align-items: flex-start;
  gap: 2.5rem;
}

#tt2{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.25rem;
  align-self: stretch;
}

#b2{
  color: #000;
  font-family: Xanh Mono;
  font-size: 2.5rem;
  font-style: italic;
  font-weight: 400;
  line-height: normal;
}

#links{
  display: flex;
  flex-wrap: wrap;
}


html, body, #root, .App{
  /*height: 100%;*/
}

.hiddenimg{
  display: none;
}

@media screen and (max-width: 1250px) {
  .container{
    flex-wrap: wrap;
    padding: 7.5rem 5.875rem;
    height: auto;
  }
}


@media screen and (min-width: 650px) {
  html, body, #root, .App{
  height: 100%;
  }
}

a:hover{
  cursor: url(outlink.png) 15 15, move;
}

 a:link, a:visited, a:active #button{
  text-decoration: none;
}
